import React, { useState } from 'react';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import axios from 'axios';

const schema = {
  title: 'ng k User',
  type: 'object',
  required: ['username', 'password'],
  properties: {
    username: { type: 'string', title: 'UserName' },
    password: { type: 'string', title: 'Password', format: 'password' },
  },
};

const UserLogin = () => {
  const [formData, setFormData] = useState({});

  const handleSubmit = async ({ formData }) => {
    try {
      console.log('D liu gi i:', formData); // Log d liu trc khi gi  kim tra
      const response = await axios.post(
        'https://us-east-1.aws.data.mongodb-api.com/app/application-0-sznak/endpoint/newUsers',
        formData
      );

      console.log('Kt qu t server:', response.data);

      // Reset form sau khi submit thnh cng
      setFormData({});
    } catch (error) {
      console.error('Li khi gi d liu:', error);
    }
  };

  return (
    <div className="auth-form-container">
      <Form
        schema={schema}
        validator={validator}
        formData={formData} // Truyn gi tr ca form
        onChange={({ formData }) => setFormData(formData)} // Cp nht state khi form thay i
        onSubmit={handleSubmit}
      />
    </div>
  );
};

export default UserLogin;